.popup {
    position: fixed;
    top:0px;
    right:0px;
    bottom:0px;
    left:0px;
    max-width: 500px;
    margin: auto;
    display: flex;
    flex-direction: column;
    z-index: 1;
    background-color:rgba(0, 0, 0, 0.8);
}

.popup-cancel {
    position: absolute;
    left: 20px;
    top: 90px;
}

.popup-container {
    margin: 30px;
    margin-top: 100px;
    background-color: black;
    border: 1px wheat solid;
    flex: 0.6;
    display: flex;
    flex-direction: column;
}

.rules {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-top: 20px;
}

.rule {
    font-size: large;
    flex: 1;
    text-align: left;
    margin-left: 10px;
    margin-right: 10px;
}

.popup-title .heading {
    font-size: larger;
    border-bottom: 1px wheat solid;
    margin: 10px;
    text-align: center;
}

.no {
    display: none;
}